import React, { Component } from 'react';
import styled from 'styled-components';

import { addBrowsePrefix } from '../util/unil'
import { config } from '../config/config'

class Experience extends Component {
 
  componentDidMount() {
    const rect = this.experience.parentNode.getBoundingClientRect();

    this.props.setProfileHeight((rect.bottom - rect.top) + 'px');
  }

  render() {
    const Experience = styled.div`
      ${addBrowsePrefix('box-sizing', 'border-box')}
      float: left;
      overflow: hidden;
      width: 823px;
      padding: 44px;
      background-color: ${config.whiteBg};

      h1 {
        text-align: left;
        color: #6c6b6b;
        font-family: ",";
      }

      .projects {
        padding: 0;
        
        >li {
          list-style: none;

          header {
            display: flex;
            justify-content: space-between;
            overflow hidden;

            .project-name {
              color: ${config.bg};
              font-size: 22px; 
              font-family: ",";
            }

            .project-skills {
              .skill {
                background-color: ${config.bg};
                padding: 5px;
                color: #FFFFFF;
                margin-left: 5px;
                float: left;
                ${addBrowsePrefix('border-radius', '10px')}
              }
            }
          }

          .project-content {
            h3 {
              padding-left: 20px;
              font-family: ",";   
            }

            li {
              color: #787878;
            }
          }
        }
      }

      .hr-bold {
        border: 2px solid ${config.hrColor};
      } 

      p {
        color: #218175;
      }
    `;

    return (
      <Experience>
        <div ref={(experience) => { this.experience = experience; }}>
          <h1></h1>
          <h3></h3>
          <hr className="hr-bold"/>
          <h1></h1>
          <hr className="hr-bold"/>
          <ul className="projects">
            <li>
              <section>
                <header>
                  <div className="project-name"></div>
                  <div className="project-skills">
                    <div className="skill">React</div>
                    <div className="skill">Antd Pro</div>
                    <div className="skill">dva</div>
                    <div className="skill">roadhog</div>
                    <div className="skill">redux</div>
                  </div>
                </header>
                <div className="project-content">
                  <article>
                    <h3></h3>
                    <ul>
                      <li> React+Antd Pro+dva+roadhog roadhog</li>
                      <li>antdantoComplete</li>
                      <li>Redux</li>
                      <li> Antd  UI  antd</li>
                    </ul>
                  </article>
                  <article>
                    <h3></h3>
                    <ul>
                      <li></li>
                    </ul>
                  </article>
                </div>
              </section>
            </li>
            <li>
              <section>
                <header>
                  <div className="project-name">App</div>
                  <div className="project-skills">
                    <div className="skill">React</div>
                    <div className="skill">webpack</div>
                    <div className="skill">Echarts</div>
                    <div className="skill">Cordova</div>
                  </div>
                </header>
                <div className="project-content">
                  <article>
                    <h3></h3>
                    <ul>
                      <li>App</li>
                      <li></li>
                      <li> lazyload  flex </li>
                      <li>rem</li>
                      <li>CSS ModulesCSSCSS</li>
                      <li>Echarts</li>
                      <li>React.PureComponent</li>
                      <li>CordovaApp</li>
                    </ul>
                  </article>
                  <article>
                    <h3></h3>
                    <ul>
                      <li>React bugbug</li>
                    </ul>
                  </article>
                </div>
              </section>
            </li>
            <li>
              <section>
                <header>
                  <div className="project-name"></div>
                  <div className="project-skills">
                    <div className="skill">Angular(5.0)</div>
                    <div className="skill">Node</div>
                    <div className="skill">TS</div>
                    <div className="skill">Electron</div>
                  </div>
                </header>
                <div className="project-content">
                  <article>
                    <h3></h3>
                    <ul>
                      <li> HTMLCSSJavaScriptAngualrAjax, ElectronNode</li>
                      <li>Router</li>
                      <li>Pipe,Service</li>
                      <li>Electron</li>
                      <li>Video + Canvas </li>
                      <li>Node</li>
                    </ul>
                  </article>
                  <article>
                    <h3></h3>
                    <ul>
                      <li>TS</li>
                      <li>Angular</li>
                      <li>  </li>
                      <li>Angular </li>
                    </ul>
                  </article>
                </div>
              </section>
            </li>
          </ul>
        </div>
      </Experience>
    );
  }
}
export default Experience;



